import React from 'react';
import './SumoLibre.css';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

const useStyles = makeStyles({
  table: {
    minWidth: 650,
  },
  tableParticipantes: {
    minWidth: 100,
  }
});


const rows = [
    createData('Frozen yoghurt', 159, 6.0, 24, 4.0),
    createData('Ice cream sandwich', 237, 9.0, 37, 4.3),
    createData('Eclair', 262, 16.0, 24, 6.0),
    createData('Cupcake', 305, 3.7, 67, 4.3),
    createData('Gingerbread', 356, 16.0, 49, 3.9),
  ];
  
function createData(name, calories, fat, carbs, protein) {
    return { name, calories, fat, carbs, protein };
}

function SumoLibre() {
    const classes = useStyles();

    return (
        <div className="SumoLibre">
            <div>Sumo Libre</div> 
            <div className="ContenerdorEnfrentamientos">  
                <img className="imgDojo" src="img/dojo.png" alt="img_dojo"/>  
                <div className="TablaDeEnfrentamientos">             
                    <TableContainer component={Paper}>
                        <Table className={classes.tableParticipantes} aria-label="simple table">
                            <TableHead>
                                <TableRow>
                                    <TableCell align="center">Participante 1</TableCell>  
                                                              
                                    <TableCell className="celda1" align="center">Participante 2</TableCell> 
                                </TableRow> 
                            </TableHead>
                        </Table>
                    </TableContainer>
                </div>
            </div> 
            <div className="ContenerdorEnfrentamientos">  
                <img className="imgDojo" src="img/dojo.png" alt="img_dojo"/>  
                <div className="TablaDeEnfrentamientos">             
                    <TableContainer component={Paper}>
                        <Table className={classes.tableParticipantes} aria-label="simple table">
                            <TableHead>
                                <TableRow>
                                    <TableCell align="center">Participante 1</TableCell>  
                                                              
                                    <TableCell className="celda1" align="center">Participante 2</TableCell> 
                                </TableRow> 
                            </TableHead>
                        </Table>
                    </TableContainer>
                </div>
            </div>  
            <div className="ContenerdorEnfrentamientos">  
                <img className="imgDojo" src="img/dojo.png" alt="img_dojo"/>  
                <div className="TablaDeEnfrentamientos">             
                    <TableContainer component={Paper}>
                        <Table className={classes.tableParticipantes} aria-label="simple table">
                            <TableHead>
                                <TableRow>
                                    <TableCell className="celda1" align="center">Participante 1</TableCell>  
                                                              
                                    <TableCell  align="center">Participante 2</TableCell> 
                                </TableRow> 
                            </TableHead>
                        </Table>
                    </TableContainer>
                </div>
            </div>  
            <br/>
            <br/>
            <TableContainer component={Paper}>
                <Table className={classes.table} aria-label="simple table">
                    <TableHead>
                        <TableRow>
                            <TableCell>Dessert (100g serving)</TableCell>
                            <TableCell align="right">Calories</TableCell>
                            <TableCell align="right">Fat&nbsp;(g)</TableCell>
                            <TableCell align="right">Carbs&nbsp;(g)</TableCell>
                            <TableCell align="right">Protein&nbsp;(g)</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {rows.map((row) => (
                            <TableRow key={row.name}>
                                <TableCell component="th" scope="row">{row.name}</TableCell>
                                <TableCell align="right">{row.calories}</TableCell>
                                <TableCell align="right">{row.fat}</TableCell>
                                <TableCell align="right">{row.carbs}</TableCell>
                                <TableCell align="right">{row.protein}</TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                </Table>
            </TableContainer> 
        </div>
    );

}

export default SumoLibre;