import React from 'react';
import firebase from '../services/firebase/setup'
import Noticia from '../components/Noticia'

function Home() {
  const [spells, setSpells] = React.useState([])

  React.useEffect(() => {
    const fetchData = async () => {
      const db = firebase.firestore()
      const data = await db.collection("Noticias").get()
      setSpells(data.docs.map(doc => doc.data()))
    }
    fetchData()
  }, [])

  return (
    <div className="Home">
      <div className="containter-fluid">
        {spells.map(spell => (
          <Noticia
          titulo={spell.titulo}
          img={spell.imgUrl}
          fecha={spell.fecha}
          text={spell.text}        
          />
        ))}
      </div>
    </div>
  );
}

export default Home;