import React from 'react';
import LeftSideBar from './animations/LeftSideBar';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom'
import './App.css';

import Home from './vistas/Home';
import MapaHospedaje from './vistas/MapaHospedaje';
import SumoLibre from './vistas/SumoLibre';

function App() {
  
  return (
    <Router>
      <div className="App">
        <LeftSideBar/>
        <Switch>
          <Route path="/app-sumo/Home"  render={() => <Home/>}/>
          <Route path="/app-sumo/MapaHospedaje" render={() => <MapaHospedaje/>}/>
          <Route path="/app-sumo/SumoLibre" render={() => <SumoLibre/>}/>
        </Switch>
      </div>
    </Router>
  );
}

export default App;
