import React from 'react';
import './Noticia.css';

function Noticia({ titulo, img, fecha, text}) {
    return(
        <div className="Marco">
            <div className="Noticia">       
                <img className="ImgNoticia" src={img}/>
                <p className="TituloNoticia">{titulo}</p>
                <p>{text}</p>
            </div>
        </div>
    );
};

export default Noticia;