import React, {useState} from 'react';
import Mapquest from '../components/Mapquest'
import './MapaHospedaje.css';


function MapaHospedaje() {
  const [lat, setLat] = useState('-34.918150884536935')
  const [lng, setLng] = useState('-56.166261434555054')
  return (
    <div className="container-fluid">
        <div>
            <p>Mapa Hospedaje</p>
        </div>
        <div className="MapaHospedaje">
          <Mapquest 
              height="80vh"
              width="100%"
              center={[lat, lng]}
              tileLayer={'map'}
              zoom={15}
              apiKey="ssMcYczg0c1zaMAhUn5PbnAshNF3Z4TR"
          />
        </div>
    </div>
  );
}

export default MapaHospedaje;
